.footer__wrapper {
  @include vp-767 {
    padding: 0 5px;
  }
}

.footer__copyright {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  color: $color-default-project;

  @include vp-1199 {
    font-size: 12px;
    font-weight: 300;
    line-height: 12px;
  }

  @include vp-767 {
    justify-content: flex-start;
  }

  @include hover-focus {
    color: $color-midnight-blue;
  }

  @include focus;

  @include active {
    color: $color-bright-blue;
  }

  @include disabled {
    color: $color-white-opacity10;
  }
}
